.app-connect {

    article.connect-card {
        margin-bottom: 56px;
        border-bottom: 1px solid $connect-divider;
    }

    button {
        margin: 24px;
    }

    article.private {
        min-height: 152px;
    }

    .connection {
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        align-items: flex-start;
        margin-bottom: 24px;
        padding: 0 24px;
        height: 48px;
        line-height: 48px;
        cursor: pointer;

        * {
            pointer-events: none;
        }

        a {
            pointer-events: auto;
            display: flex;
            flex-direction: row;
            align-items: flex-start;
            justify-content: flex-start;
            flex: 0 0 auto;
            text-decoration: none;
        }

        .anchor,
        .text {
            line-height: 24px;
            margin: 12px 0;
            font-size: 20px;
            color: $theme-blue;
        }

        .anchor {
            color: $theme-blue;
            transition: transform 0.5s ease-out;
            margin-right: 24px;
        }

        .url {
            line-height: 24px;
            height: 24px;
            margin: 12px 0;
            min-width: 50%;
            opacity: 0;
            transition: opacity 0.5s, transform 0.5s ease-out;
            padding-left: 4px;
            font-size: 16px;
            background-color: transparent;
            border-color: $connect-divider;
            border-radius: 12px;
            color: $connect-secondary;
            outline: none;
            text-decoration: none;
            pointer-events: auto;
            display: none;
        }
        
        icon {
            margin: 12px;
        }

        .connection-logo {
            width: 48px;
            height: 48px;
            font-size: 0;
            margin-right: 12px;
            
            img {
                max-width: 48px;
                max-height: 48px;
            }
        }

        .anim-logo {
            height: 48px;
            width: 48px;
            display: block;
            position: relative;
            border-radius: 100%;
            color: $theme-blue;
            fill: $theme-blue;
            border-radius: 100%;
            margin-right: 24px;
            
            svg {
                color: inherit;
                fill: inherit;
                height: 48px;
                width: 48px;
            }

            &.anim-npm-logo {
                svg {
                    width: 80%;
                    margin: 0 10%;
                    .background {
                        fill: $theme-blue;
                    }

                    .foreground {
                        fill: $theme-tan;
                    }
                }
            }

            &.anim-phone-logo,
            &.anim-email-logo {
                svg {
                    height: 24px;
                    width: 24px;
                    margin: 12px;
                }
            }
        }


    }

    @media screen and (min-width: 768px) {

        button {
            margin: 56px;
        }

        .connection {
            padding: 0 56px;

            .url {
                display: block;
            }

            &:hover {
                
                .anchor {
                    text-decoration: underline;
                    transform: translateX(25px);
                }
                
                .url {
                    transform: translateX(50px);
                    opacity: 1;
                }

                .anim-logo {
                    background: $theme-blue;
                    transition: background-color 0.5s, transform 0.5s ease-out;
                    transform: scale(1.25);
                    fill: $connect-fill;
                    color: $connect-fill;
                }

                .anim-npm-logo {
                    svg {
                        .background {
                            fill: $theme-blue;
                            transition: fill 0.5s;
                        }
                        
                        .foreground {
                            fill: $theme-tan;
                            transition: fill 0.5s;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (max-width: 1200px) {

    .platform-ios {
        container {
            navigation {
                opacity: 0;
                visibility: hidden;
                transition: opacity 0.3s ease.0s;
            }

            &.offset-main-right {
                navigation {
                    opacity: 1;
                    visibility: visible;
                }
            }

            &.animate-main-right {
                navigation {
                    opacity: 1;
                    visibility: visible;
                }
            }

            &.animate-main-default {
                navigation {
                    opacity: 0;
                    visibility: hidden;
                }
            }
        }
    }
}


