.ui-materialize-logo {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    &:before {
        content: "ui";
        display: block;
        color: deeppink;
        font-weight: bold;
        font-size: 16px;
        line-height: 20px;
    }

    &:after {
        content: "Materialize";
        display: block;
        color: dimgrey;
        font-weight: normal;
        font-size: 20px;
    }
}

.domx-logo {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;

    &:before {
        content: "<";
        display: block;
        color: darkorange;
        font-weight: bold;
        font-size: 30px;
        line-height: 24px;
    }

    &:after {
        content: "dom-x";
        display: block;
        color: indigo;
        font-weight: normal;
        font-size: 30px;
    }
}

